/* Gallery Container */
.gallery {
  display: grid;
  grid-template-columns: repeat(3, 1fr); 
  gap: 20px;
  justify-content: center;
  align-items: center;
  width: 90%;
  max-width: 1200px; 
  margin: 0 auto;
  margin-top: 20px;
}

/* Gallery Image */
/* Gallery Image */
.gallery-image {
  width: 100%; 
  height: 200px; 
  object-fit: cover; 
  cursor: pointer;
  border-radius: 10px;
}


/* Full-Screen Modal */
.fullscreen-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 1000;
}

.fullscreen-image {
  max-width: 90%;
  max-height: 90%;
  object-fit: contain;
  border: 2px solid white;
  border-radius: 10px;
}

/* Close Button */
.close-buttons {
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
  color: white;
  background-color: red;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.close-buttons:hover {
  background-color: darkred;
}

/* Media Query for Larger Images on Web */
@media (min-width: 768px) {
  .gallery {
    grid-template-columns: repeat(2, 1fr); 
  }

  .gallery-image {
    width: 100%; 
    height: 200px; 
    object-fit: cover;
  }
}

@media (min-width: 1024px) {
  .gallery {
    grid-template-columns: repeat(3, 1fr); 
  }
}
