/* Global Styles */
body {
  font-family: 'Arial', sans-serif;
  background: linear-gradient(135deg, #ffafbd, #ffc3a0);
  margin: 0;
  padding: 0;
  font-size: 16px; 
}

.app {
  text-align: center;
  padding: 20px;
}

/* Header */
.header h1 {
  color: #ff3366;
  font-size: 2.5rem;
  margin-bottom: 10px;
}

.header p {
  font-size: 1.2rem;
  color: black;
}

/* Gift Section */
.gift-section {
  margin: 20px 0;
}

.gift {
  font-size: 1.8rem; 
  margin: 20px auto;
  cursor: pointer;
  padding: 1rem; 
  background: #fff;
  border-radius: 10px;
  color: #ff3366;
  text-align: center;
  max-width: 100%; 
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}


/* Gift Section Image Grid */
.gift-images {
  display: grid;
  grid-template-columns: repeat(3, 1fr); 
  gap: 10px; 
  justify-items: center; 
  margin: 0 auto; 
  max-width: 1200px; 
  padding: 20px; 
}

/* Styling for each individual image */
.gift-image {
  width: 100%; 
  height: 150px; 
  object-fit: cover; 
  border-radius: 10px; 
  cursor: pointer; 
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}


/* Hover effect for image zoom */
.gift-image:hover {
  transform: translateY(-5px);
  box-shadow: 0px 6px 8px rgba(0, 0, 0, 0.2);
}

/* Media Queries for Tablet and Mobile View */
@media (max-width: 768px) {
  .gift-images {
    grid-template-columns: repeat(3, 1fr); 
    gap: 15px; 
  }

  .header h1 {
    font-size: 1.8rem;
  }

  .header p {
    font-size: 1rem;
  }

  .gift {
    font-size: 1.5rem;
    padding: 1.2rem;
  }

  .gift-content img {
    max-width: 90%; 
  }
}

@media (max-width: 480px) {
  .gift-images {
    grid-template-columns: repeat(3, 1fr); 
    gap: 10px; 
  }

  .gift {
    font-size: 1.2rem; 
    padding: 1rem;
  }
}


/* Full-Screen Modal Styles */
.full-screen-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  flex-direction: column; 
}

.full-screen-modal img {
  max-width: 90%;
  max-height: 80%;
  object-fit: contain;
  cursor: pointer;
}

/* Close Button Styles */
.close-button {
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #ff3366;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 1.2rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.close-button:hover {
  background-color: #ff0044;
}





