/* Countdown Container */
.countdown {
  background-color: #fdf6e3;
  border: 2px solid #f4a261;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
  margin: 20px auto;
  max-width: 450px; 
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.countdown:hover {
  transform: translateY(-5px);
  box-shadow: 0px 6px 8px rgba(0, 0, 0, 0.2);
}

/* Countdown Heading */
.countdown h2 {
  color: #e76f51;
  font-family: 'Poppins', sans-serif;
  margin-bottom: 10px;
  font-size: 1.8em;
}

/* Countdown Timer Text */
.countdown p {
  font-size: 2rem;
  color: #2a9d8f;
  font-weight: bold;
}

/* Responsive Design for Mobile */
@media (max-width: 768px) {
  .countdown {
    padding: 15px;
    margin: 10px;
  }

  .countdown h2 {
    font-size: 1.5em; 
  }

  .countdown p {
    font-size: 1.5rem; 
  }
}
